import React from 'react';
import { 
  Card, 
  CardContent, 
  FormControl, 
  InputLabel, 
  Select, 
  MenuItem, 
  Typography, 
  Box,
  Chip,
  Collapse,
  Divider,
  Grid
} from '@mui/material';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import InfoIcon from '@mui/icons-material/Info';
import MemoryIcon from '@mui/icons-material/Memory';
import TimelineIcon from '@mui/icons-material/Timeline';
import NeuralNetworkIcon from '@mui/icons-material/Hub';  // Using Hub icon for neural network

// Dictionary for mapping numeric codes to human-readable values (same as in other components)
const valueMappings = {
  Gender: {
    0: 'Male',
    1: 'Female'
  },
  Ethnicity: {
    0: 'Caucasian',
    1: 'African American',
    2: 'Asian',
    3: 'Other'
  },
  EducationLevel: {
    0: 'None',
    1: 'High School',
    2: 'Bachelor\'s',
    3: 'Higher'
  },
  Smoking: {
    0: 'No',
    1: 'Yes'
  },
  FamilyHistoryAlzheimers: {
    0: 'No',
    1: 'Yes'
  },
  CardiovascularDisease: {
    0: 'No',
    1: 'Yes'
  },
  Diabetes: {
    0: 'No',
    1: 'Yes'
  },
  Depression: {
    0: 'No',
    1: 'Yes'
  },
  HeadInjury: {
    0: 'No',
    1: 'Yes'
  },
  Hypertension: {
    0: 'No',
    1: 'Yes'
  },
  MemoryComplaints: {
    0: 'No',
    1: 'Yes'
  },
  BehavioralProblems: {
    0: 'No',
    1: 'Yes'
  },
  Confusion: {
    0: 'No',
    1: 'Yes'
  },
  Disorientation: {
    0: 'No',
    1: 'Yes'
  },
  PersonalityChanges: {
    0: 'No',
    1: 'Yes'
  },
  DifficultyCompletingTasks: {
    0: 'No',
    1: 'Yes'
  },
  Forgetfulness: {
    0: 'No',
    1: 'Yes'
  }
};

const ModelSelector = ({ models, selectedModel, onChange }) => {
  const [expandedModel, setExpandedModel] = React.useState(null);

  if (!models || Object.keys(models).length === 0) {
    return null;
  }

  const handleExpandClick = (modelName) => {
    setExpandedModel(expandedModel === modelName ? null : modelName);
  };

  const formatParameterValue = (param, value) => {
    // Check if this parameter has a human-readable mapping
    if (valueMappings[param] && valueMappings[param][value] !== undefined) {
      return valueMappings[param][value];
    }
    
    // Otherwise format as before
    if (typeof value === 'number') {
      // Format number to 3 decimal places if it has decimals
      return Number.isInteger(value) ? value : value.toFixed(3);
    }
    return value;
  };

  // Get model display name
  const getModelDisplayName = (name) => {
    switch(name) {
      case 'rf': return 'Random Forest';
      case 'logistic': return 'Logistic Regression';
      case 'xgboost': return 'XGBoost';
      case 'svm': return 'SVM';
      case 'knn': return 'KNN';
      case 'nn': return 'Neural Network';
      default: return name;
    }
  };

  return (
    <Card sx={{ 
      mb: 3, 
      boxShadow: '0 4px 8px rgba(0,0,0,0.1)',
      bgcolor: 'rgba(25, 118, 210, 0.03)',
      borderTop: '4px solid #1976d2'
    }}>
      <CardContent>
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
          <MemoryIcon color="primary" sx={{ mr: 1 }} />
          <Typography variant="h6" color="primary">
            ML Model Selection
          </Typography>
        </Box>
        
        <Divider sx={{ mb: 2 }} />
        
        <FormControl fullWidth variant="outlined" sx={{ mb: 2 }}>
          <InputLabel id="model-select-label">Select Prediction Model</InputLabel>
          <Select
            labelId="model-select-label"
            id="model-select"
            value={selectedModel}
            label="Select Prediction Model"
            onChange={onChange}
          >
            {Object.entries(models).map(([name, details]) => {
              // Get display name using our helper function
              const displayName = getModelDisplayName(name);
              
              return (
                <MenuItem key={name} value={name}>
                  <Box sx={{ display: 'flex', alignItems: 'center', width: '100%', justifyContent: 'space-between' }}>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      {details.is_best && <CheckCircleIcon color="success" fontSize="small" sx={{ mr: 0.5 }} />}
                      {name === 'nn' && <NeuralNetworkIcon fontSize="small" sx={{ mr: 0.5, color: 'secondary.main' }} />}
                      <span>{displayName}</span>
                    </Box>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <Chip 
                        label={`Acc: ${(details.accuracy * 100).toFixed(1)}%`} 
                        size="small" 
                        color={details.is_best ? "success" : "primary"}
                        variant={details.is_best ? "filled" : "outlined"}
                        sx={{ mr: 1 }}
                      />
                      {details.roc_auc && (
                        <Chip 
                          label={`ROC: ${(details.roc_auc * 100).toFixed(1)}%`} 
                          size="small" 
                          color={details.is_best ? "success" : "secondary"}
                          variant={details.is_best ? "filled" : "outlined"}
                        />
                      )}
                    </Box>
                  </Box>
                </MenuItem>
              );
            })}
          </Select>
        </FormControl>
        
        {selectedModel && models[selectedModel] && (
          <Box sx={{ mt: 2 }}>
            {models[selectedModel].is_best && (
              <Box sx={{ 
                display: 'flex', 
                alignItems: 'center', 
                p: 1, 
                bgcolor: 'rgba(76, 175, 80, 0.1)', 
                borderRadius: 1,
                mb: 2 
              }}>
                <CheckCircleIcon color="success" fontSize="small" sx={{ mr: 1 }} />
                <Typography variant="body2" color="success.dark">
                  This is the best performing model based on ROC AUC score
                </Typography>
              </Box>
            )}
            
            {/* Model metrics display */}
            <Box sx={{ mb: 2 }}>
              <Grid container spacing={2}>
                <Grid item xs={6}>
                  <Box sx={{ 
                    display: 'flex', 
                    flexDirection: 'column', 
                    alignItems: 'center',
                    p: 1,
                    borderRadius: 1,
                    bgcolor: 'rgba(25, 118, 210, 0.05)'
                  }}>
                    <Typography variant="caption" color="text.secondary">Accuracy</Typography>
                    <Typography variant="h6" color="primary">
                      {(models[selectedModel].accuracy * 100).toFixed(1)}%
                    </Typography>
                  </Box>
                </Grid>
                <Grid item xs={6}>
                  <Box sx={{ 
                    display: 'flex', 
                    flexDirection: 'column', 
                    alignItems: 'center',
                    p: 1,
                    borderRadius: 1,
                    bgcolor: 'rgba(156, 39, 176, 0.05)'
                  }}>
                    <Typography variant="caption" color="text.secondary">ROC AUC</Typography>
                    <Typography variant="h6" color="secondary">
                      {models[selectedModel].roc_auc 
                        ? (models[selectedModel].roc_auc * 100).toFixed(1) + '%' 
                        : 'N/A'}
                    </Typography>
                  </Box>
                </Grid>
              </Grid>
            </Box>
            
            {/* Model type specific info */}
            {selectedModel === 'nn' && (
              <Box sx={{ 
                display: 'flex', 
                alignItems: 'center', 
                p: 1,
                mb: 2,
                bgcolor: 'rgba(156, 39, 176, 0.05)', 
                borderRadius: 1
              }}>
                <NeuralNetworkIcon color="secondary" fontSize="small" sx={{ mr: 1 }} />
                <Typography variant="body2" color="text.secondary">
                  Deep neural network with multiple hidden layers
                </Typography>
              </Box>
            )}
            
            {/* Check for parameters using both keys for backward compatibility */}
            {((models[selectedModel].parameters && Object.keys(models[selectedModel].parameters).length > 0) || 
              (models[selectedModel].params && Object.keys(models[selectedModel].params).length > 0)) && (
              <Box sx={{ mt: 2 }}>
                <Box 
                  onClick={() => handleExpandClick(selectedModel)}
                  sx={{ 
                    display: 'flex', 
                    alignItems: 'center', 
                    cursor: 'pointer',
                    padding: '4px 8px',
                    borderRadius: '4px',
                    '&:hover': {
                      backgroundColor: 'rgba(0, 0, 0, 0.04)'
                    }
                  }}
                >
                  <InfoIcon fontSize="small" sx={{ mr: 1, color: 'primary.main' }} />
                  <Typography variant="body2" color="primary">
                    Model Hyperparameters (Tuned)
                  </Typography>
                  {expandedModel === selectedModel ? 
                    <ExpandLessIcon fontSize="small" sx={{ ml: 1 }} /> : 
                    <ExpandMoreIcon fontSize="small" sx={{ ml: 1 }} />
                  }
                </Box>
                
                <Collapse in={expandedModel === selectedModel}>
                  <Box 
                    sx={{ 
                      mt: 1, 
                      ml: 2,
                      padding: 1.5,
                      backgroundColor: 'rgba(0, 0, 0, 0.02)',
                      borderRadius: 1
                    }}
                  >
                    {/* Use parameters or params, whichever is available */}
                    {Object.entries(models[selectedModel].parameters || models[selectedModel].params || {}).map(([param, value]) => (
                      <Typography key={param} variant="body2" sx={{ mb: 0.5 }}>
                        <strong>{param}:</strong> {formatParameterValue(param, value)}
                      </Typography>
                    ))}
                  </Box>
                </Collapse>
              </Box>
            )}
          </Box>
        )}
      </CardContent>
    </Card>
  );
};

export default ModelSelector; 