import React from 'react';
import { 
  Card, 
  CardContent, 
  Typography, 
  Box,
  Chip
} from '@mui/material';
import WarningIcon from '@mui/icons-material/Warning';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';

// Dictionary for mapping numeric codes to human-readable values (same as in PatientForm)
const valueMappings = {
  Gender: {
    0: 'Male',
    1: 'Female'
  },
  Ethnicity: {
    0: 'Caucasian',
    1: 'African American',
    2: 'Asian',
    3: 'Other'
  },
  EducationLevel: {
    0: 'None',
    1: 'High School',
    2: 'Bachelor\'s',
    3: 'Higher'
  },
  Smoking: {
    0: 'No',
    1: 'Yes'
  },
  FamilyHistoryAlzheimers: {
    0: 'No',
    1: 'Yes'
  },
  CardiovascularDisease: {
    0: 'No',
    1: 'Yes'
  },
  Diabetes: {
    0: 'No',
    1: 'Yes'
  },
  Depression: {
    0: 'No',
    1: 'Yes'
  },
  HeadInjury: {
    0: 'No',
    1: 'Yes'
  },
  Hypertension: {
    0: 'No',
    1: 'Yes'
  },
  MemoryComplaints: {
    0: 'No',
    1: 'Yes'
  },
  BehavioralProblems: {
    0: 'No',
    1: 'Yes'
  },
  Confusion: {
    0: 'No',
    1: 'Yes'
  },
  Disorientation: {
    0: 'No',
    1: 'Yes'
  },
  PersonalityChanges: {
    0: 'No',
    1: 'Yes'
  },
  DifficultyCompletingTasks: {
    0: 'No',
    1: 'Yes'
  },
  Forgetfulness: {
    0: 'No',
    1: 'Yes'
  }
};

const PredictionResult = ({ prediction }) => {
  const isHighRisk = prediction.risk === 'High';
  
  // Helper function to format model name nicely
  const formatModelName = (modelName) => {
    const modelMappings = {
      'rf': 'Random Forest',
      'xgboost': 'XGBoost',
      'logistic': 'Logistic Regression',
      'svm': 'Support Vector Machine',
      'knn': 'K-Nearest Neighbors',
      'nn': 'Neural Network'
    };
    
    return modelMappings[modelName] || modelName;
  };
  
  return (
    <Card sx={{ 
      mb: 3, 
      bgcolor: isHighRisk ? 'rgba(244, 67, 54, 0.05)' : 'rgba(76, 175, 80, 0.05)',
      borderLeft: isHighRisk ? '4px solid #f44336' : '4px solid #4caf50',
      boxShadow: '0 4px 8px rgba(0,0,0,0.1)'
    }}>
      <CardContent>
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', mb: 2 }}>
          <Typography variant="h6" color={isHighRisk ? 'error' : 'success'} sx={{ fontWeight: 'bold' }}>
            {isHighRisk ? 'High Risk' : 'Low Risk'}
          </Typography>
          <Chip 
            icon={isHighRisk ? <WarningIcon /> : <CheckCircleIcon />}
            label={prediction.probability ? `${(prediction.probability * 100).toFixed(1)}%` : ''}
            color={isHighRisk ? 'error' : 'success'}
          />
        </Box>
        
        <Box sx={{ 
          display: 'flex', 
          flexDirection: 'column', 
          p: 2, 
          borderRadius: 1, 
          bgcolor: 'background.paper',
        }}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
            <Typography variant="body2" color="text.secondary">
              Model:
            </Typography>
            <Typography variant="body2" fontWeight="bold">
              {formatModelName(prediction.model_type || "Unknown")}
            </Typography>
          </Box>
          
          <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
            <Typography variant="body2" color="text.secondary">
              Accuracy:
            </Typography>
            <Typography variant="body2" fontWeight="bold">
              {(prediction.accuracy * 100).toFixed(1)}%
            </Typography>
          </Box>
          
          {prediction.roc_auc && (
            <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
              <Typography variant="body2" color="text.secondary">
                ROC AUC:
              </Typography>
              <Typography variant="body2" fontWeight="bold">
                {(prediction.roc_auc * 100).toFixed(1)}%
              </Typography>
            </Box>
          )}
        </Box>
      </CardContent>
    </Card>
  );
};

export default PredictionResult; 